// Home page

.stat-list{
  @extend %listreset;
  font-size:$base-font-size * 1.17; //20px
  @extend %clearfix;
  padding: 4px 0 47px;
  @include media("<=tablet"){
    font-size:$base-font-size;
    padding: 0 0 20px;
  }
  li{
    float: left;
    width: 50%;
    padding: 0 15px 10px 0;
    display: flex;
    align-items: center;
    @include media("<=tablet"){
      float: none;
      width: 100%;
      padding: 0 0 5px;
    }
  }
  .icon-box{
    margin: 0 10px 0 0;
  }
  .icon{
    font-size:$base-font-size * 3; //52px
    @include media("<=tablet"){
      font-size:$base-font-size * 2.2;
    }
  }
  .text{
    display: inline-block;
    padding: 0 0 12px;
  }
}
.content-section{
  padding: 30px 0;
  &.gray-bg{
    background: $gray;
    .content-block{
      .text-block{
        line-height: 1.3;
        h2{
          margin: 0 0 13px;
        }
      }
    }
  }
}

.js-tab-hidden {
  display: block !important;
  left: -9999px !important;
  position: absolute !important;
  top: -9999px !important;
}
.tabs-holder{
  padding: 26px 0 41px;
  .content-block{
    padding: 0 0 21px;
  }

}
.content-block{
  display: flex;
  align-items: center;
  justify-content: space-between;
  @include media("<=tablet"){
    flex-direction: column;
  }
  .image-block{
    float: left;
    width: 50.7%;
    margin: 0 0 0 -10px;
    &:last-child{
      margin: 0 -23px 0 0;
      @include media("<=tablet"){
        margin: 0;
      }
    }
    @include media("<=tablet"){
      width: 100%;
      text-align: center;
      padding: 0 0 10px;
    }
  }
  .text-block{
    overflow: hidden;
    width: 43.3%;
    padding: 0 0 23px;
    @include media("<=tablet"){
      width: 100%;
      text-align: justify;
    }
    h2{
      margin: 0 0 16px;
      @include media("<=tablet"){
        text-align: center;
      }
    }
    h3{
      text-transform: uppercase;
      color: $base-gray;
      font-size: $h3-font-size * 0.8; //20px;
      font-weight: 600;
      margin: 0 0 24px;
      @include media("<=tablet"){
        text-align: center;
      }
    }
    p{
      margin: 0 0 46px;
      @include media("<=tablet"){
        margin: 0 0 20px;
      }
    }
  }
}
.button-block{
  overflow: hidden;
  text-align: center;
  .btn{
    position: relative;
    padding: 9px 31px 10px 39px;
    &:before,
    &:after{
      @include pseudo();
      width: 999px;
      height: 1px;
      background: $gray2;
      top: 50%;
      left:-1041px;
      transform: translate(0, -50%);
    }
    &:after{
      left: auto;
      right: -1041px;
    }
  }
}
.customers{
  padding: 57px 0 71px;
}
.customers-list{
  @extend %listreset;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: space-between;
  margin: 50px -15px 19px;
  @include media("<=tablet"){
    margin: 20px -15px 15px;
  }
  @include media("<=phone"){
    margin: 15px 0;
  }
  li{
    width: 25%;
    padding: 0 15px 30px;
    @include media("<=tablet"){
      width: 50%;
    }
    @include media("<=phone"){
      width: 100%;
      padding: 0 0 15px;
    }
  }
  .customer-logo{
    display: flex;
    @include size(100%);
    border: 1px solid $gray2;
    align-items: center;
    justify-content: center;
    padding: 18px 28px;
    height: 144px;
    img{
      max-height:100%;
    }
    @include media("<=tablet"){
      height: 115px;
    }
    @include media("<=phone"){
      height: auto;
    }
  }
}
.help-section{
  background-size: cover;
  background-position: center;
  position: relative;
  text-align: center;
  color: $white;
  font-size:$base-font-size * 2.12; //36px
  padding: 5.1% 0 5.25%;
  @include media("<=tablet"){
    font-size:$base-font-size * 1.5; //25.5
  }
  &:before{
    @include pseudo();
    @include size(100%);
    background:rgba(0,0,0,.4) url(../images/bg-help-before.png) no-repeat center 100%;
    top:0;
    left:0;
  }
  *{
    position: relative;
    z-index: 3;
  }
  .text{
    p{
      margin: 0 0 29px;
    }
    .btn{
      font-size:$base-font-size * 1.06; //18px;
      display: block;
      max-width: 292px;
      margin: 0 auto;
    }
  }
}
